import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { formatDate, InputGroupInline, parseRefToDate, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { DATES } from '../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportFormView from '../../../report/ReportFormView';
import { CLASS_KEY, PrestacaoServicoRV } from '../prestacaoServico/prestacaoServicoRV';

const REPORT_EVENT = 'printPrestacaoServicoGeral';
const REPORT_TITLE = 'Listagem de Prestação de Serviço';

export default function PrestacaoServicoPEDReportView(props: WindowComponentProps) {
  const history = useHistory();

  const formProps = useForm<PrestacaoServicoRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const empresaMLF = globalReducer.globalParameter.empresaMLF;

  const basicData: PrinterDataBasic = {
    ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, DATES.value),
    dtFinal: formatDate(parseRefToDate(empresaMLF.referenciaAtual, 0)),
    dtInicial: formatDate(parseRefToDate(empresaMLF.referenciaAtual, 1)),
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null) {
        const id = globalReducer.globalParameter.empresa.id;
        setTimeout(() => {
          formProps.setValue('armazenarPaginaInicial', false);
          formProps.setValue('modelo', true);
          formProps.setValue('idGerEmpresa', id);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportFormView formProps={formProps} printerData={basicData} history={history} extraConfigOpened={true}>
        <InputGroupInline formProps={formProps} id="armazenarPaginaInicial" hidden={true} />
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <InputGroupInline formProps={formProps} id="modelo" hidden={true} />
      </ReportFormView>
    </WindowComponent>
  );
}
